.gallery{
  position: relative;
  float: left;
  width: 100%;
  margin-top: 100px;
  background-image: url("../img/bg-gallery.png");

    &__title{
      position: relative;
      float: left;
      width: 100%;
      text-align: center;
      color: #5a4a65;
      font-family: $QanelasHeavy;
      font-size: 75px;
      font-weight: 800;
      margin-bottom: 75px;

        &:before{
          position: absolute;
          display: block;
          content: "";
          width: 108px;
          height: 119px;
          background-image: url("../img/gallery.png");
          background-repeat: no-repeat;
          left: 348px;
          top: -42px;
        }
    }

  &__slider{
    position: relative;
    float: left;
    width: 100%;
    margin-bottom: 185px;
  }

  &__slide{
    position: relative;
    float: left;
    width: 100%;
  }

  &__img-wrap{
    position: relative;
    float: left;
    width: 355px;
    height: 225px;
    overflow: hidden;
    border: 3px solid #5a4a65;
    border-radius: 10px;

      img{
        width: 100%;
        object-fit: contain;
      }
  }
}

.slick-prev{
position: relative;
  float: left;

  &:before{
    position: absolute;
    display: block;
    content: "";
    width: 42px;
    height: 42px;
    background-image: url("../img/icons/arrow-prev.png");
    background-repeat: no-repeat;
    top: 285px;
    left: 554px;
  }
}

.slick-next{
  position: relative;
  float: left;



  &:before{
    position: absolute;
    display: block;
    content: "";
    width: 42px;
    height: 42px;
    background-image: url("../img/icons/arrow-next.png");
    background-repeat: no-repeat;
    left: 560px;
    top: 60px;
  }
}