.callback{
  position: relative;
  float: left;
  width: 100%;
  height: 545px;
  background-image: url("../img/form-bg.png");
  background-repeat: no-repeat;
  background-size: cover;

    &__form{
      position: relative;
      float: left;
      width: 555px;
      height: 501px;
      background-color: white;
      border-radius: 10px;
      box-shadow: 1.5px 2.6px 57px rgba(90, 74, 101, 0.71);
      top: 55px;
      left: 462px;
      padding-left: 80px;
      padding-right: 80px;
      box-sizing: border-box;
    }

    &__title{
      position: relative;
      float: left;
      width: 100%;
      text-align: center;
      color: #5a4a65;
      font-family: $QanelasBold;
      font-size: 33px;
      font-weight: 700;
    }

  &__request{
    position: relative;
    float: left;
    width: 100%;
  }

  &__name{
    position: relative;
    float: left;
    width: 100%;
    margin-bottom: 55px;
    margin-top: 40px;
    border: none;
    color: #5a4a65;
    font-family: $QanelasRegular;
    font-size: 20px;
    font-weight: 400;

    &::-webkit-input-placeholder {
      color: #5a4a65;
      font-family: $QanelasRegular;
      font-size: 20px;
      font-weight: 400;
    }
  }

  &__elem-name{
    position: relative;
    float: left;

    &:before{
      position: absolute;
      display: block;
      content: "";
      width: 10px;
      height: 12px;
      background-image: url("../img/icons/user.png");
      background-repeat: no-repeat;
      top: 48px;
      left: -19px;
      z-index: 1;
    }

    &:after{
      position: absolute;
      display: block;
      content: "";
      width: 446px;
      height: 1px;
      background-color: #666;
      top: 83px;
      left: -24px;
    }
  }

  &__phone{
    position: relative;
    float: left;
    width: 100%;
    border: none;
    color: #5a4a65;
    font-family: $QanelasRegular;
    font-size: 20px;
    font-weight: 400;

    &::-webkit-input-placeholder {
      color: #5a4a65;
      font-family: $QanelasRegular;
      font-size: 20px;
      font-weight: 400;
    }
  }

  &__elem-phone{
    position: relative;
    float: left;

    &:before{
      position: absolute;
      display: block;
      content: "";
      width: 13px;
      height: 10px;
      background-image: url("../img/icons/msg.png");
      background-repeat: no-repeat;
      top: 134px;
      left: -414px;
      z-index: 1;
    }

    &:after {
      position: absolute;
      display: block;
      content: "";
      width: 446px;
      height: 1px;
      background-color: #666;
      left: -418px;
      top: 170px;
    }
  }

  &__descr{
    position: relative;
    float: left;
    width: 100%;
    text-align: center;
    color: #5a4a65;
    font-family: $QanelasRegular;
    font-size: 12px;
    font-weight: 500;
  }

  &__elem-submit{
    position: relative;
    float: left;

    &:after{
      position: absolute;
      display: block;
      content: "";
      width: 24px;
      height: 16px;
      background-image: url("../img/icons/button-arrow.png");
      background-repeat: no-repeat;
      top: 24px;
      right: -70px;
    }
  }
}

.checkbox{
  position: relative;
  float: left;
  width: 100%;
  margin-top: 30px;
}

.checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}
.checkbox__text {
  position: relative;
  float: left;
  width: 100%;
  text-align: center;
  color: #5a4a65;
  font-family: $QanelasBold;
  font-size: 16px;
  font-weight: 700;
}
.checkbox__text:before {
  position: absolute;
  content: "";
  display: block;
  top: 4px;
  left: 44px;
  width: 13px;
  height: 13px;
  background-image: url("../img/icons/form.png");
  background-repeat: no-repeat;
}

.checkbox input:checked + .checkbox__text:before {
background-color: #5a4a65;
  z-index: 1;
}

input[type = submit]{
  top: -36px;
  left: 92px;
}


